.movie-view{
	border-radius: 10px;
	box-shadow: 20px 20px 50px #000;
	color: white;	
}

.movie-title {
	padding: 25px;
	font-size: 2.5em;
  display: flex;
  font-weight: bold;
  justify-content: center;
	width: 100%;

}
.movie-description{
	padding: 25px;
	font-size: 1em;
}

.movie-col {
	margin: 0 !important;
	padding: 0 !important;
	background-color: #000 !important;	
}

.movie-image {
   width: 100%;
   height: auto;
   align-items: center;
}

.movie-genre{
	padding-left: 25px;
	font-weight: bold;
	font-size: 1em;
}

.movie-director{
	padding-left: 25px;
	font-size: 1em;
}

.back-row{
	align-items: flex-end;
  height: 150px;
}